.about {
  @include page-grid;
  &__title {
    grid-column: 1/-1;

    @include on-tablet {
      grid-column: 1/4;
    }

    @include on-desktop {
      grid-column: 1/7;
    }
  }

  &__description {
    grid-column: 1/-1;

    @include on-tablet {
      grid-column: 4/7;
    }

    @include on-desktop {
      grid-column: 7/13;
    }

    font-family: "Space Mono", monospace;
    font-size: 16px;
    font-weight: 400;
    line-height: 22.4px;
    margin-top: 0;
  }
}
