.section-title {
  margin: 0;
  margin-bottom: 48px;
  font-family: "Space Grotesk", sans-serif;
  font-size: 32px;
  font-weight: 700;
  line-height: 32px;
  text-align: left;

  @include on-tablet {
    font-size: 40px;
    margin-bottom: 56px;
  }

  @include on-desktop {
    font-size: 48px;
  }

  &__about {
    margin-bottom: 24px;
  }
}

